import React from 'react';
import {useParams} from 'react-router-dom';
import {useGetData} from "../../Providers/CourseProvider";
import CourseCard from "../../Atomics/CourseCard";
import Section from "../../Atomics/Section";
import CourseCarousel from "../../Atomics/CourseCarousel";

const Module = () => {
    const {courseId, moduleGroupId, moduleId} = useParams();
    const exercises = useGetData('Exercises', moduleId);

    const constructExercises = () => {
        const temp = [];

        for(let key in exercises) {
            const {title, description, symbol, id} = exercises[key];

            temp.push(
                <CourseCard key={id ?? 0} slim title={title} symbol={symbol} to={`/course/${courseId}/${moduleGroupId}/${moduleId}/${id}`}>
                    {description}
                </CourseCard>
            )
        };

        return temp;
    };

    return (
        <Section title={'ModulÃ¼bersicht'}>
            <CourseCarousel>
                {constructExercises()}
            </CourseCarousel>
        </Section>
    );
};

export default Module;